/*  Diaries blog page
*
*  Version:
*    1.0.0 - 2021/12/14
*/

.c-diaries-template {
  position: relative;
  padding-top: 8rem;
  padding-bottom: 3.8125rem;
  z-index: 0;

  &__header-background {
    position: absolute;
    left: 0;
    width: 100%;
    top: 0;
    padding-bottom: 39.5rem;
    background-color: $s-color--background-tertiary;
    z-index: -1;
  }

  &__inner {
    @include t-gutters;
  }

  &__content-container {
    text-align: center;
    margin: 0 auto;
  }

  &__heading {
    color: $s-color--font-6;
    text-align: center;
    max-width: 74.375rem;
    width: 100%;
    margin: 1.875rem auto 1.5625rem;
  }

  &__description {
    margin: 0 auto;
    line-height: 1.125rem;
    max-width: 40.25rem;
    width: 89%;
  }

  &__btn {
    margin-top: 2.375rem;
    margin-bottom: 2.3125rem;
  }

  &__image {
    &-container {
      padding-bottom: 122%;
    }

    &.is-desktop {
      display: none;
    }
  }

  &__articles {
    margin-top: 5.25rem;
    @include t-gutters;
  }

  &__articles-wrapper {
    margin-bottom: 3.9375rem;
  }

  .o-pagination {
    margin-bottom: 0;
  }

  @include t-media-query($s-small-up) {
    &__image-container {
      margin: 0 auto;
      max-width: 107.5rem;
      width: 80%;
      padding-bottom: 53%;
    }

    &__articles {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-column-gap: 4.1563rem;
      grid-row-gap: 1.5625rem
    }
  }

  @include t-media-query($s-medium-up) {
    &__inner {
      padding-top: 3.5%;
      padding-bottom: 3%;
      display: flex;
    }

    &__content-container {
      text-align: left;
      width: 50%;
    }

    &__image-container {
      width: 50%;
      padding-bottom: 50%;
    }

    &__heading {
      text-align: left;
    }

    &__description {
      margin-left: 0;
    }

    &__image {
      &.is-mobile {
        display: none;
      }

      &.is-desktop {
        display: block;
      }
    }
  }

  @include t-media-query($s-large-up) {
    &__header-background {
      padding-bottom: 47rem;
    }

    &__inner {
      padding-bottom: 2%;
    }

    &__image-container {
      width: 56%;
      padding-bottom: 42%;
    }

    &__content-container {
      margin: 3.0625rem 0;
      width: 44%;
    }

    &__heading {
      margin: 2.8125rem auto 1.4375rem;
    }

    &__description {
      line-height: 1.625rem;
    }

    &__btn {
      margin-top: 1.75rem;
    }

    &__articles {
      grid-template-columns: repeat(3, 1fr);
      grid-column-gap: 6.0313rem;
      grid-row-gap: 3.4375rem
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__header-background {
      padding-bottom: 50rem;
    }
  }
}